import { useState } from "react"
import { Typography } from "@rp/react/web/mui/core"
import { Check, Link } from "@rp/react/web/mui/icons"

type Props = {
  url?: string
}
const ShareCopyLink = ({ url }: Props) => {
  const [copy, setCopy] = useState(false)

  const handleCopyLink = () => {
    if (!url) {
      return
    }
    window.navigator.clipboard.writeText(url)
    setCopy(true)
    setTimeout(() => setCopy(false), 1000)
  }

  return (
    <Typography
      alignItems="center"
      color="primary.main"
      display="flex"
      fontSize={14}
      gap={1}
      onClick={handleCopyLink}
      sx={{ cursor: "pointer" }}
      data-testid="copy-link"
    >
      {copy ? (
        <>
          <Check sx={{ fontSize: "20px" }} />
          Copied
        </>
      ) : (
        <>
          <Link sx={{ fontSize: "20px" }} />
          Copy link
        </>
      )}
    </Typography>
  )
}
export { ShareCopyLink }
